  renderAnalytics() {
    if (this.analytics.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-chart-bar"></i>
          <p>ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ≠ŸÑŸäŸÑŸäÿ©</p>
        </div>
      `;
    }

    return `
      <div class="analytics-dashboard">
        ${this.analytics
          .map(
            analytic => `
          <div class="analytic-card">
            <div class="analytic-header">
              <h3>${analytic.metric}</h3>
              <span class="analytic-value">${analytic.value}</span>
            </div>
            <div class="analytic-body">
              <p>${analytic.description || ''}</p>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.type !== 'all') {
      filtered = filtered.filter(item => item.type === this.filters.type);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.plateNumber && item.plateNumber.toLowerCase().includes(search)) ||
          (item.name && item.name.toLowerCase().includes(search)) ||
          (item.model && item.model.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [vehicles, routes, trips, drivers, schedules, maintenance, analytics] =
        await Promise.all([
          this.apiClient.get(
            this.API_ENDPOINTS.vehicles || '/api/advanced-transportation/vehicles'
          ),
          this.apiClient.get(this.API_ENDPOINTS.routes || '/api/advanced-transportation/routes'),
          this.apiClient.get(this.API_ENDPOINTS.trips || '/api/advanced-transportation/trips'),
          this.apiClient.get(this.API_ENDPOINTS.drivers || '/api/advanced-transportation/drivers'),
          this.apiClient.get(
            this.API_ENDPOINTS.schedules || '/api/advanced-transportation/schedules'
          ),
          this.apiClient.get(
            this.API_ENDPOINTS.maintenance || '/api/advanced-transportation/maintenance'
          ),
          this.apiClient.get(
            this.API_ENDPOINTS.analytics || '/api/advanced-transportation/analytics'
          ),
        ]);

      this.vehicles = vehicles.data || [];
      this.routes = routes.data || [];
      this.trips = trips.data || [];
      this.drivers = drivers.data || [];
      this.schedules = schedules.data || [];
      this.maintenance = maintenance.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading transportation data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-transportation', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('transportationContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      active: 'ŸÜÿ¥ÿ∑',
      inactive: 'ÿ∫Ÿäÿ± ŸÜÿ¥ÿ∑',
      maintenance: 'ÿµŸäÿßŸÜÿ©',
      reserved: 'ŸÖÿ≠ÿ¨Ÿàÿ≤',
      scheduled: 'ŸÖÿ¨ÿØŸàŸÑ',
      inprogress: 'ŸÇŸäÿØ ÿßŸÑÿ™ŸÜŸÅŸäÿ∞',
      completed: 'ŸÖŸÉÿ™ŸÖŸÑ',
      cancelled: 'ŸÖŸÑÿ∫Ÿâ',
      pending: 'ŸÇŸäÿØ ÿßŸÑÿßŸÜÿ™ÿ∏ÿßÿ±',
      done: 'ŸÖŸÜÿ¨ÿ≤',
    };
    return statusMap[status] || status;
  }

  getVehicleTypeText(type) {
    const typeMap = {
      bus: 'ÿ≠ÿßŸÅŸÑÿ©',
      van: 'ŸÅÿßŸÜ',
      car: 'ÿ≥Ÿäÿßÿ±ÿ©',
      ambulance: 'ÿ•ÿ≥ÿπÿßŸÅ',
    };
    return typeMap[type] || type;
  }

  getDayText(day) {
    const dayMap = {
      sunday: 'ÿßŸÑÿ£ÿ≠ÿØ',
      monday: 'ÿßŸÑÿ•ÿ´ŸÜŸäŸÜ',
      tuesday: 'ÿßŸÑÿ´ŸÑÿßÿ´ÿßÿ°',
      wednesday: 'ÿßŸÑÿ£ÿ±ÿ®ÿπÿßÿ°',
      thursday: 'ÿßŸÑÿÆŸÖŸäÿ≥',
      friday: 'ÿßŸÑÿ¨ŸÖÿπÿ©',
      saturday: 'ÿßŸÑÿ≥ÿ®ÿ™',
    };
    return dayMap[day] || day;
  }

  formatCurrency(amount) {
    return new Intl.NumberFormat('ar-SA', {
      style: 'currency',
      currency: 'SAR',
      minimumFractionDigits: 2,
    }).format(amount);
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatDateTime(dateTime) {
    if (!dateTime) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(dateTime).toLocaleString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedVehicles', JSON.stringify(this.vehicles));
      localStorage.setItem('advancedRoutes', JSON.stringify(this.routes));
      localStorage.setItem('advancedTrips', JSON.stringify(this.trips));
      localStorage.setItem('advancedDrivers', JSON.stringify(this.drivers));
      localStorage.setItem('advancedSchedules', JSON.stringify(this.schedules));
      localStorage.setItem('advancedMaintenance', JSON.stringify(this.maintenance));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.vehicles = JSON.parse(localStorage.getItem('advancedVehicles') || '[]');
      this.routes = JSON.parse(localStorage.getItem('advancedRoutes') || '[]');
      this.trips = JSON.parse(localStorage.getItem('advancedTrips') || '[]');
      this.drivers = JSON.parse(localStorage.getItem('advancedDrivers') || '[]');
      this.schedules = JSON.parse(localStorage.getItem('advancedSchedules') || '[]');
      this.maintenance = JSON.parse(localStorage.getItem('advancedMaintenance') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createVehicle = this.createVehicle.bind(this);
    this.createTrip = this.createTrip.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewVehicle = this.viewVehicle.bind(this);
    this.editVehicle = this.editVehicle.bind(this);
  }

